.Title{
    text-transform: uppercase;
    color: white;
    font-size: 1.3rem;
    position: relative;
    padding-bottom: 1rem;
}
.Title::after{
    content: "";
    background-color: rgba(0, 0, 255, 0.473);
    width: 11rem;
    height: 0.3rem;
    left: 0;
    bottom: 0;
    position: absolute;
}
.Title::before{
    content: "";
    background-color: blue;
    width: 4rem;
    height: 0.3rem;
    left: 0;
    bottom: 0;
    position:absolute;
}
.Title span{
    color: blue;
    position: absolute;
    top: 10%;
    left: 0;
    font-size: 3rem;
    opacity: 0.2;
}
.NavBar{
    height: 100vh;
}
.SkillsSection{
    width: 100%;
    
}

.nav {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    font-size: 1rem;
    margin-top: 0.4rem;
    
}
.profile{
    width: 100%;
    text-align: center;
    margin-top: 1rem;
    border-bottom: 2px solid rgb(56, 56, 56);
}

.nav-item{
    list-style-type: none;
    /* text-align: center; */
    /* position: relative; */
}
.nav-item a {
    text-decoration: none;
    font-size: inherit;
    display: block;
    color: inherit;
    padding: 0.2rem 0.3rem;
    position: relative;
}
.nav-item a::before{
    content: "";
    position: absolute;
    /* top: 0;
    left: 0;
    width: .4rem;
    height: 100%; */
    overflow: hidden;
}
.nav-item a:hover{
    color: rgb(146, 146, 146);
    width: 100%;
    
}
img{
    width: 60%;
    border-radius: 100%;
    margin-bottom: 1rem;
}
footer{
    width: 100%;
}
footer p{
    text-align: center;
