* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Roboto', sans-serif;
    color: #ffffff;
    background-color: black;
    /* height: fit-content; */
    /* font-size: 1.2rem; */
}

.side-bar{
    width: 20%;
    height: 100vh;
    background-color: #191D2B;
    position: fixed;
    z-index: 11;
    /* transition: all 0.4s ease-in-out; */
}
.main-content{
    width: 80%;
    /* min-height: 100vh; */
    height: 100vh;
    margin-left: 20%;
    /* position: fixed; */
    background-color: black;
    position: relative;
    display: grid;
    /* height: fit-content; */
}
.content{
    margin: 1rem 2rem;
}

.pro-text{
    font-size: 0.6rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.pro-text a{
    color: rgb(247, 97, 97);
}


/* typography for main-content */

.Home{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 80%;
}
.main-text{
    text-align: center;
    font-size: 2rem;
}
.main-text span{
    color: rgb(255, 0, 0);
   font-family: 'Syne Mono', monospace;
}

.home-para{
    text-align: center;
    font-size: 0.8rem;
}

.icons-lists{
    padding: 2rem 2rem;
    -webkit-align-items: center;
            align-items: center;
    display: grid;
    grid-template-columns: repeat(3,1fr);
}
.icons-lists li{
    list-style-type: none;
    border:2px solid rgb(158, 158, 158);
    padding: 1rem 1rem;
}
.icons-item div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}
.icons-item div a{
    text-decoration: none;
    color: white;
    font-size: 1.4rem;
    cursor: pointer;
}
.icon-text{
    font-size: 0.7rem;
}

@media screen and (max-width:1164px){
    .main-text{
    font-size: 1.5rem;
     }
     .icons .icons-item{
        font-size: .7rem;
    }
    .icon-text{
        font-size: 0.2rem;
    }
    .icons-lists{
    padding: 1rem 1rem;
    -webkit-align-items: center;
            align-items: center;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    }
    .icons-lists li{
    padding: 0.5rem 0.6rem;
}
.icons-item div a{
    font-size: 1.3rem;
}
}
@media screen and (max-width:700px){
    .main-text{
    font-size: 1.2rem;
     }
    .side-bar{
        -webkit-transform: translateX(-130%);
                transform: translateX(-130%);
        padding: 1rem 1rem;
    }
    .main-content{
        margin-left: 0;
        width: 100%;
    }
    .icons-item div a{
    font-size: 1.2rem;
    }
}



@media screen and (max-width:700px){
