        padding: 0.6rem 0.6rem;
        -webkit-align-items: center;
                align-items: center;
        display: grid;
        grid-template-columns: repeat(2,1fr);
    } 
    .icons-item div{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-align-items: center;
